import { Component, OnInit } from '@angular/core';
import { ServAPIService } from 'src/app/service/serv-api.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
   
  menu:any[] = [];
  cart:any[]=[];
  totalPrice:number = 0

  constructor( private servAPI : ServAPIService) {}
  
  ngOnInit(): void {
    this.getData()
  }

 //To get All Data From APIServ File 
  getData(){
    this.servAPI.getData().subscribe(
      (res:any) => {
        console.log(res)
        this.menu=res?.burgers

      }, err => {
        console.log(err)
      }
    )
  }

  addToCart(item:any) {
    let existed = false
    for (let i = 0 ; i < this.cart?.length ; i++) {
      if(item?.id == this.cart[i]?.id) {
        existed=true
        this.cart[i].quantity+=1
        break;
      }
    }
    if(!existed) {
    this.cart.push({...item , quantity:1 });
      console.log(this.cart)
    }
    this.getTotal()
  }
  
  getTotal() {
    if(this.cart?.length) {
      this.totalPrice=0
      for(let i = 0 ; i < this.cart.length;i++) {
        this.totalPrice+=this.cart[i].quantity*this.cart[i].id
      }
    } else  {
      this.totalPrice=0
    }
  }

deleteItem(index:number) {
  this.cart.splice(index,1)
  this.getTotal()
  console.log(this.cart)
}
}

